import { Component, OnInit } from '@angular/core';
import { ToastController } from "@ionic/angular"
import { ActivatedRoute } from '@angular/router'
import { AppMin } from "../core"

@Component({
  selector: 'app-mywallet-cash',
  templateUrl: './mywallet-cash.page.html',
  styleUrls: ['./mywallet-cash.page.scss'],
})

export class MywalletCashPage implements OnInit {
  money;
  mymoney;
  phone;
  code;
  header_class;
  isDisabled: boolean = false;
  smitBtnTxt = "发送验证码";
  constructor(public AppMin: AppMin, public ToastController: ToastController, public ActivatedRoute: ActivatedRoute) {
    this.phone = this.AppMin.getUserPhoneNumber();
    this.mymoney = this.ActivatedRoute.snapshot.queryParams['mymoney'];
    // console.log('mymoney='+this.mymoney);
  }

  ngOnInit() {

  }
  async send_phone_code() {
    let this1 = this;

    if (this.phone == '') {
      this.toast('请输入手机号');
      return;
    }

    let obj = {
      type: "sendVerificationCode",
      phonenumber: this.phone
    }

    //this.send(JSON.stringify(obj), false);

    let sendTime = 59;
    this.isDisabled = true;
    let sendInterval = setInterval(() => {


      sendTime -= 1;
      this.smitBtnTxt = "已发送(" + sendTime + ")";

      if (sendTime == 0) {
        this.isDisabled = false;
        this.smitBtnTxt = "发送验证码";
        clearInterval(sendInterval);
      }
      
    }, 1000)

    this.AppMin.get(obj).subscribe((data) => {

      this1.toast("已发送验证码");
    })
  }
  async toast(msg) {
    let toast = await this.ToastController.create({
      message: msg,
      duration: 1500,
      position: 'top'
    });
    toast.present();
  }
  subm() {

    if (this.code == '' || this.code == undefined) {
      this.toast('请输入正确的验证码');
      return;
    }

    let money = parseFloat(this.money)
    if (money <= 0 || isNaN(money)) {
      this.toast('请输入正确的提现金额');
      return;
    }
    if (money > this.mymoney) {
      this.toast('余额不足');
      return;
    }

    let this1 = this;
    let obj = {
      type: 'withdrawalsMoney',
      userId: this.AppMin.getUserid(),
      money: this.money,
      code: this.code,
      phone: this.phone
    }
    this.AppMin.post(obj).subscribe((data) => {

      if (data['code'] == '200') {
        this1.toast("提交成功");
        history.go(-1);
      } else {
        this1.toast(data['msg']);
      }

    })
  }

}
